.snipet-gallery__section-title{
    font-family: 'Petit Formal Script', cursive;
    font-size: 24px;
    text-align: center;
}

.snippet-gallery__wrapper{
    margin: auto;
    padding: 50px 0;    
    text-align: center;
}
.snippet-gallery__image-wrapper{
    cursor: pointer;
    position: relative;
    width: 100%;      
    overflow: hidden;   
    margin: 5px 0;   
}

.snippet-gallery__image-wrapper.one{    
    height: 210px;      
}
.snippet-gallery__image-wrapper.two{
    height: 425px;
}

.snippet-gallery__image{
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
}

.snippet-gallery__overlay{
    background-color:rgba(36, 36, 62, .75);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-transition: opacity .5s ease;
    -o-transition: opacity .5s ease;
    transition: opacity .5s ease;
}

@media (hover: hover) {
    .snippet-gallery__image-wrapper:hover .snippet-gallery__overlay{
        opacity: 0;
        -webkit-transition: opacity .5s ease;
        -o-transition: opacity .5s ease;
        transition: opacity .5s ease;
    }

    .snippet-gallery__image-wrapper:hover .snippet-gallery__image{
        -webkit-transform: scale(1.1,1.1);
            -ms-transform: scale(1.1,1.1);
                transform: scale(1.1,1.1);
        -webkit-transition: all .5s ease;
        -o-transition: all .5s ease;
        transition: all .5s ease;
    }

    .snippet-gallery__image-wrapper:hover .snippet-galley__image-caption-wrapper{ 
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
        opacity: 0;
        -webkit-transition: all .5s ease;
        -o-transition: all .5s ease;
        transition: all .5s ease;
    }
    
    .snippet-gallery__image-wrapper:hover p{
        left: 0;
        -webkit-transition: all .5s ease;
        -o-transition: all .5s ease;
        transition: all .5s ease;
    }
}

.image-caption__place,
.image-caption__date{
    margin: 0;
    font-family: 'Roboto', sans-serif;
    position: absolute;
    left: 10px;
}

.image-caption__place{
    font-size: 24px;
    font-weight: bold;    
    bottom: 15px;
    color: #dedede;
}

.image-caption__date{
    font-size: 12px;
    bottom: 45px;
    color: #ccc;
}

.snippet-galley__image-caption-wrapper{
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
    top: 10px;    
    border: 1px solid #CCC;
}

.snipet-gallery__row{    
    /* float: left; */
    display: inline-block;
    width: 32.33%;
    padding: 0 5px;
    -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
    box-sizing: border-box;         /* Opera/IE 8+ */
}

.snippet-gallery__wrapper{    
    /* width: 100%; */
    margin: 0 auto 0;
    /* height: 100%; */
    padding: 10px;
}

.snippet-gallery__wrapper:before, .snippet-gallery__wrapper:after {
    content: "";
    display: table;
}

.snippet-gallery__wrapper:after {
    clear: both;
}

.snippet-gallery__wrapper {
    zoom: 1;
}

.snippet-gallery__modal-wrapper{
    display: none;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: #040101b5; /* Black w/ opacity */
}

.snippet-gallery__modal-wrapper.open{
    display: block;
}

.snippet-gallery__modal-content{    
    position: relative;
    margin: 15px auto; /* 15% from the top and centered */        
    width: 50%; /* Could be more or less, depending on screen size */    
    -webkit-animation-name: modalIn;    
            animation-name: modalIn;
    -webkit-animation-duration: 0.6s;
            animation-duration: 0.6s
}
.potrait .snippet-gallery__modal-content{
    height: 100%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

/* Add Animation */
@-webkit-keyframes modalIn {
    from {top: -300px; opacity: 0}
    to {top: 0; opacity: 1}
}
@keyframes modalIn {
    from {top: -300px; opacity: 0}
    to {top: 0; opacity: 1}
}

.snippet-gallery__modal-content .close{
    margin: 0;
    position: absolute;    
    right: -24px;;
    color: #dedede;
    font-size: 24px;
    cursor: pointer;
}

.snippet-gallery__image-name{
    color: #dedede;
    margin: 0;
}

.landscape .snippet-gallery__modal-content img {
    height: auto;
    width: 100%;
}

.potrait .snippet-gallery__modal-content img{
    height: 100%;
    width: auto;
}

@media only screen and (min-width: 1100px) {
    .snippet-gallery__wrapper{    
        max-width: 1100px;
    }
}

@media only screen and (max-width: 1099px) {
    .snippet-gallery__wrapper{    
        max-width: 900px;
    }
}

@media only screen and (max-width: 899px) {
    .snippet-gallery__wrapper{    
        max-width: 699px;
    }
}

@media only screen and (max-width: 700px) {
    .snippet-gallery__wrapper{
        height: auto;
    }    
}

@media only screen and (max-width: 425px) {
    .potrait .snippet-gallery__modal-content{
        height: 60%;
    }
    .potrait .snippet-gallery__modal-content img{
        height: 100%;
        width: auto;
    }
    .snippet-gallery__modal-content{
        margin: 50% auto;
        width: 80%;
    }
    .snipet-gallery__row{
        display: block;
        width: 100%;
    }
}


