.header__sticky-header{
    background-color: #B22222;
    color: #FFF;    
    border-bottom: 1px solid #800000;
    text-align: center;    
    display: block;
    width: 100%;
    -webkit-box-shadow: 0px 3px 25px 0px rgb(178, 34, 34);
    box-shadow: 0px 3px 25px 0px rgb(178, 34, 34);
    margin: 0;
    padding: 10px 0;
    position: absolute;
    /* bottom: 0; */
    /* left: 0; */
}

.header__inline-nav-wrapper{
    padding: 0;
}

.header__inline-nav-wrapper li{
    display: inline-block;
    margin: 0 10px;
    padding: 10px;        
}   
.header__inline-nav-wrapper li a{
    color: #FFF;
    text-decoration: none;
}

.header__title{
    font-family: 'Petit Formal Script', cursive;
}

.header__sticky-header.stick{
    position: fixed;
    top: 0;
    left: 0;
    bottom: unset;
    width: 100%;
    z-index: 1;
}

.header__mobile-menu-wrapper .header__title{
    position: fixed;
    top: 10px;
    z-index: 10;
    -webkit-animation-name: bouncingIn;
            animation-name: bouncingIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
    opacity: 0;
    -webkit-animation-fill-mode: forwards; /* Chrome 16+, Safari 4+ */    /* FF 5+ */      /* Not implemented yet */     /* IE 10+ */
    animation-fill-mode: forwards;         /* When the spec is finished */
}

.header__mobile-menu-wrapper .header__title.out{
    opacity: 1;    
    -webkit-animation-name: bouncingOut;    
            animation-name: bouncingOut;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;    
    -webkit-animation-fill-mode: forwards; /* Chrome 16+, Safari 4+ */    /* FF 5+ */      /* Not implemented yet */     /* IE 10+ */
    animation-fill-mode: forwards;         /* When the spec is finished */
}

@-webkit-keyframes bouncingIn {
    from{        
        -webkit-transform: translateY(50px);        
                transform: translateY(50px);
        opacity: 0;        
    }
    to{        
        -webkit-transform: translateY(0);        
                transform: translateY(0);
        opacity: 1;        
    }
}

@keyframes bouncingIn {
    from{        
        -webkit-transform: translateY(50px);        
                transform: translateY(50px);
        opacity: 0;        
    }
    to{        
        -webkit-transform: translateY(0);        
                transform: translateY(0);
        opacity: 1;        
    }
}

@-webkit-keyframes bouncingOut {
    from{        
        -webkit-transform: translateY(0);        
                transform: translateY(0);
        opacity: 1;        
    }
    to{        
        -webkit-transform: translateY(50px);        
                transform: translateY(50px);
        opacity: 0;        
    }
}

@keyframes bouncingOut {
    from{        
        -webkit-transform: translateY(0);        
                transform: translateY(0);
        opacity: 1;        
    }
    to{        
        -webkit-transform: translateY(50px);        
                transform: translateY(50px);
        opacity: 0;        
    }
}

.header__mobilemenu-wrapper{
    z-index: 10;
    list-style-type: none;
    padding: 0;    
    -webkit-animation-name: bouncingIn;    
            animation-name: bouncingIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;    
    opacity: 0;
    -webkit-animation-fill-mode: forwards; /* Chrome 16+, Safari 4+ */    /* FF 5+ */      /* Not implemented yet */     /* IE 10+ */
    animation-fill-mode: forwards;         /* When the spec is finished */
}

.header__mobilemenu-wrapper.out{    
    opacity: 1;    
    -webkit-animation-name: bouncingOut;    
            animation-name: bouncingOut;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;    
    -webkit-animation-fill-mode: forwards; /* Chrome 16+, Safari 4+ */    /* FF 5+ */      /* Not implemented yet */     /* IE 10+ */
    animation-fill-mode: forwards;         /* When the spec is finished */
}

.header__mobilemenu-wrapper li{
    margin-bottom: 10px;
}
@-webkit-keyframes slideIn{
    from{
        -webkit-transform: skew(10deg) translateX(100%);
                transform: skew(10deg) translateX(100%);
    }
    to{
        -webkit-transform: skew(10deg) translateX(0);
                transform: skew(10deg) translateX(0);
    }
}
@keyframes slideIn{
    from{
        -webkit-transform: skew(10deg) translateX(100%);
                transform: skew(10deg) translateX(100%);
    }
    to{
        -webkit-transform: skew(10deg) translateX(0);
                transform: skew(10deg) translateX(0);
    }
}

@-webkit-keyframes slideInReverse{
    from{
        -webkit-transform: skew(10deg) translateX(-100%);
                transform: skew(10deg) translateX(-100%);
    }
    to{
        -webkit-transform: skew(10deg) translateX(0);
                transform: skew(10deg) translateX(0);
    }
}

@keyframes slideInReverse{
    from{
        -webkit-transform: skew(10deg) translateX(-100%);
                transform: skew(10deg) translateX(-100%);
    }
    to{
        -webkit-transform: skew(10deg) translateX(0);
                transform: skew(10deg) translateX(0);
    }
}

.cta-close{
    opacity: 0;
    -webkit-animation-name: bouncingIn;
            animation-name: bouncingIn;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
    -webkit-animation-fill-mode: forwards; /* Chrome 16+, Safari 4+ */    /* FF 5+ */      /* Not implemented yet */     /* IE 10+ */
    animation-fill-mode: forwards;         /* When the spec is finished */
}

.cta-close.out{
    opacity: 1;
    -webkit-animation-name: bouncingOut;
            animation-name: bouncingOut;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
    -webkit-animation-fill-mode: forwards; /* Chrome 16+, Safari 4+ */    /* FF 5+ */      /* Not implemented yet */     /* IE 10+ */
    animation-fill-mode: forwards;         /* When the spec is finished */
}

.header__mobile-menu-bg.right{    
    -webkit-animation-name: slideIn;    
            animation-name: slideIn;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.header__mobile-menu-bg.left{    
    -webkit-animation-name: slideInReverse;    
            animation-name: slideInReverse;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
}

.header__mobile-menu-bg.right.out{
    -webkit-transform: skew(10deg) translateX(-100%);
        -ms-transform: skew(10deg) translateX(-100%);
            transform: skew(10deg) translateX(-100%);
    -webkit-transition: -webkit-transform 2s ease;
    transition: -webkit-transform 2s ease;
    -o-transition: transform 2s ease;
    transition: transform 2s ease;
    transition: transform 2s ease, -webkit-transform 2s ease;
    -webkit-transition-delay: .5s;
         -o-transition-delay: .5s;
            transition-delay: .5s;
}

.header__mobile-menu-bg.left.out{
    -webkit-transform: skew(10deg) translateX(100%);
        -ms-transform: skew(10deg) translateX(100%);
            transform: skew(10deg) translateX(100%);
    -webkit-transition: -webkit-transform 2s ease;
    transition: -webkit-transform 2s ease;
    -o-transition: transform 2s ease;
    transition: transform 2s ease;
    transition: transform 2s ease, -webkit-transform 2s ease;
    -webkit-transition-delay: .5s;
         -o-transition-delay: .5s;
            transition-delay: .5s;
}

.menu-toggler{
    display: none;
}

@media only screen and (max-width: 425px) {
    .header__inline-nav-wrapper li{
        display: none;
    }

    .header__inline-nav-wrapper li.header__title{
        display: block;
    }
    .menu-toggler{
        display: block;
    }
}


