.mepandes__wrapper{
    margin-top: 25px;
}
.mepandes__section-title{
    font-family: 'Petit Formal Script', cursive;
    font-size: 24px;
    text-align: center;
    margin: 10px 0 0;
}

.mepandes__person-wrapper{
    display: inline-block;
    position: relative;
}

.mepandes__person-wrapper.bubble{
    width: 33.33%;
    text-align: center;
}

.mepandes__person-img{
    height: 300px;
    width: 370px; 
}

.mepandes__person-img img{
    height: 100%;
    width: 100%;
}

@media only screen and (max-width: 500px) {
    .mepandes__person-wrapper.bubble{
        width: 50%;    
    }
}

@media only screen and (max-width: 425px) {
    .mepandes__person-wrapper.bubble{
        width: 100%;    
    }
}


