.greeting__section{    
    /* width: 100%; */
    margin: 0 auto 0;
    /* height: 100%; */
    padding: 10px;
    padding-top: 100px;
}

.greeting__section:before, .greeting__section:after {
    content: "";
    display: table;
}

.greeting__section:after {
    clear: both;
}

.greeting__section {
    zoom: 1;
}

.greeting__left-side{
    z-index: -1;
    float: left;
    width: 40%;
    background-size: cover;
    background-position: center;    
    height: 450px;    
    border-radius: 5px;
    position:relative;
    -webkit-box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;
    box-shadow:0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset;    
}

