.greeting__wrapper{
    float: left;
    width: 60%;
    height: 350px;
    padding: 20px 0;
}
.greeting__paragraph,
.greeting__family-head{
    font-family: 'Roboto', sans-serif;
    padding: 0 20px;
    text-align: center;
    margin: 0 auto 15px;
    font-size: 14px;
}

.greeting__family-head.left{
    float: left;
}

.greeting__family-head.right{
    float: right;
}

@media only screen and (min-width: 1100px) {
    .greeting__section{    
        max-width: 1000px;
    }
}

@media only screen and (max-width: 1099px) {
    .greeting__section{    
        max-width: 900px;
    }
}

@media only screen and (max-width: 899px) {
    .greeting__section{    
        max-width: 699px;
    }
}

@media only screen and (max-width: 700px) {
    .greeting__section{
        height: auto;
    }
    .greeting__left-side{
        float: none;
        width: 100%;
        height: 300px;
    }    
    .greeting__wrapper{
        float: none;
        width: 100%;
    }
}


